body {
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.4;
  color: #fff;
  background-color: #fff;
}

img,
video {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

b,
strong {
  font-weight: 700;
}

input,
button,
select,
textarea {
  font: inherit;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}

.container-lg {
  margin: 0 auto;
  padding: 0 calc(var(--container-offset) * 2);
  width: 100%;
  max-width: 1920px;
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: 100%;
}

.container-md {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: 1356px;
}

.container-sm {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: 1190px;
}

.container-xs {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: 1130px;
}

.hamburger {
  padding: 20px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  background-image: url(../img/menu_bg.svg);
  position: relative;
  z-index: 10;
}

.hamburger:hover {
  opacity: 0.7;
}

.hamburger.is-active {
  background-image: none;
}

.hamburger.is-active:hover {
  opacity: 0.7;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}

.hamburger-box {
  width: 34px;
  height: 18px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 34px;
  height: 4px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -10px;
}

.hamburger-inner::after {
  bottom: -10px;
}

.hamburger--slider .hamburger-inner {
  top: 2px;
}

.hamburger--slider .hamburger-inner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}

.hamburger--slider .hamburger-inner::after {
  top: 20px;
}

.hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(45deg);
}

.hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}

.hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg);
}

body {
  background-color: #141429;
}

.header {
  padding-top: 40px;
  position: fixed;
  left: 0;
  width: 100%;
  top: 0;
  z-index: 9;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav-list {
  display: flex;
  align-items: center;
  gap: 75px;
}
.header__mobile-nav {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: 0.4s ease;
  -o-transition: 0.4s ease;
  transition: 0.4s ease;
  cursor: pointer;
  position: fixed;
  top: 0;
  right: -100vw;
  bottom: 0;
  z-index: -10;
}
.header__mobile-nav::after {
  display: block;
  content: "";
  background-color: #D02B2B;
  background-repeat: no-repeat;
  width: 0;
  height: 0;
  border-radius: 50%;
  transition: 0.4s ease;
  opacity: 0;
  cursor: auto;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  transform: translate(50%, -23%);
}
.header__mobile-nav.active {
  display: flex;
  justify-content: flex-end;
  visibility: visible;
  opacity: 1;
  z-index: 9;
  left: 0;
  right: 0;
}
.header__mobile-nav.active::after {
  width: 100vw;
  height: 100vw;
  opacity: 1;
  z-index: 9;
}
.header__mobile-nav.active .header__mobile-nav-list {
  opacity: 1;
}
.header__mobile-nav-list {
  max-width: 50vw;
  width: 100%;
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
}
.header__mobile-nav-item {
  margin-bottom: 35px;
}
.header__mobile-nav-link {
  font-family: "Risque", sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 37px;
  text-align: left;
}
.header__nav-link {
  padding-bottom: 5px;
  background: linear-gradient(currentColor 0 0) var(--p, 0) 100%/var(--d, 0) 3px no-repeat;
  transition: 0.3s, background-position 0s 0.3s;
}
.header__nav-link:hover {
  --d: 100%;
  --p: 100%;
}

.section-hero {
  background-color: #141429;
}

.hero {
  background-color: #141429;
}
.hero .fp-overflow {
  height: 100%;
}
.hero::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: -224px;
  width: 448px;
  height: 448px;
  background-image: url("../img/snowflake@4x.png");
  z-index: 1;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  animation: slowRotate 40s linear infinite;
}
.hero__inner {
  display: flex;
  height: 100%;
  justify-content: space-between;
  margin-top: auto;
  position: relative;
}
@media screen and (max-width: 1024px) {
  .hero__inner {
    flex-direction: column;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  padding-left: 10%;
  padding-top: 17.3%;
}
@media screen and (max-width: 1300px) {
  .hero__text {
    width: 40%;
  }
}
@media screen and (max-width: 1200px) {
  .hero__text {
    width: 20%;
  }
}
@media screen and (max-width: 1024px) {
  .hero__text {
    width: 100%;
  }
}
.hero__image {
  align-self: flex-end;
  overflow: hidden;
  position: relative;
}
@media screen and (max-width: 1300px) {
  .hero__image {
    width: 60%;
  }
}
@media screen and (max-width: 1200px) {
  .hero__image {
    width: 70%;
  }
}
@media screen and (max-width: 1024px) {
  .hero__image {
    width: 100%;
  }
}
.hero__title {
  font-family: "Risque", sans-serif;
  font-size: 72px;
  line-height: 83px;
  text-align: left;
  margin-bottom: 20px;
  white-space: nowrap;
}
.hero__subtitle {
  font-family: "Risque", sans-serif;
  font-size: 38px;
  line-height: 44px;
  text-align: left;
  margin-bottom: 26px;
  white-space: nowrap;
}
.hero__button-action {
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  background-color: transparent;
  padding: 0;
  margin: 0;
  border: 2px solid #fff;
  border-radius: 20px 20px 20px 0;
  width: 186px;
  height: 44px;
  cursor: pointer;
  color: #fff;
  transition: all 0.3s ease-in-out;
}
.hero__button-action:hover {
  background-color: #fff;
  color: #000;
}
.hero__button-scroll {
  width: 34px;
  height: 34px;
  background-image: url("../img/arrow.svg");
  background-size: contain;
  background-position: right bottom;
  background-repeat: no-repeat;
  background-color: transparent;
  border: 0;
  padding: 0;
  margin: 0;
  cursor: pointer;
  position: absolute;
  left: 10%;
  bottom: 5%;
  animation: moveUpDown 2s infinite ease-in-out;
}
.hero__image-text {
  font-family: "Risque", sans-serif;
  font-size: 18px;
  line-height: 21px;
  text-align: left;
  width: 260px;
  height: 170px;
  background-image: url("../img/cloud@4x.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 35px;
  color: #000;
  position: absolute;
  pointer-events: none;
  top: 160px;
  left: 110px;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}
.hero__image-text.active {
  opacity: 1;
  animation: textOpacity 3s ease-in-out;
}
.hero__image-picture {
  width: 880px;
  min-width: 880px;
}
@media screen and (max-width: 1024px) {
  .hero__image-picture {
    width: 100%;
    min-width: initial;
  }
}

@keyframes moveUpDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes slowRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes textOpacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}